import React, {  } from 'react';
import { Link } from "react-router-dom";
import DelayLink from 'react-delay-link';

const Header = ({isMainNav, isMain, isSub, mainClosing, isWork, isHistory, workState, historyState}) => {

	const workAction = () =>{
		if(isWork) {
      return;
		}
		if(isMain) {
			mainClosing();
		}
	}
	const historyAction = () =>{
		if(isHistory) {
      return;
		}
		if(isWork) {
			workState(false);
		}
	}
	return(
		<header className={"pageHeader" + (isMainNav ? ' navigation' : '') + (isMain ? ' isMain' : '') + (isSub ? ' isSub' : '')}>
			<h1><Link to="/"><strong><em>Work</em></strong> <span><em>done so far</em></span></Link></h1>
			<p>- PORTFOLIO -</p>
			<ul>	
				<li>
					<DelayLink delay={800} to="/work" clickAction={workAction} replace={false}>WORK</DelayLink>
					{/* <NavLink to="/work" activeClassName="selected">WORK</NavLink> */}
				</li>
				<li>
					<DelayLink delay={800} to="/history" clickAction={historyAction} replace={false}>HISTORY</DelayLink>
					{/* <NavLink to="/history" activeClassName="selected">HISTORY</NavLink> */}
				</li>
			</ul>
		</header>
	)
}
export default Header;